.App {
  text-align: center;
}

.imgIcon {
  transition: 1s;
}
.imgIcon:hover {
  transform: scale(1.2);
}

.enable {
  opacity: 1;
  transition: 1s;
}

.not-enable {
  opacity: 0;
  transition: 1s;
}

.enableCircle {
  transform: scale(1.2);
  transition: 1s;
}
.not-enableCircle {
  transform: scale(1);
  transition: 1s;
}
